@charset "UTF-8";
@import "../components/_essay-index-aside.scss";
$bfsize:10px;
$leftWidth:310px;


//#region override bootstrap
@media (min-width: 1200px) {
    .container, .container-lg, .container-md, .container-sm, .container-xl {
        max-width: 1240px;
    }
}
//#endregion

@mixin defaulta {
    a {
        text-decoration: none;

        &:link {
            color: #000;
        }

        &:visited {
            color: #555;
        }

        &:hover {
            color: #337ab7;
        }
    }
}

.essay-main {
    width: 100%;
    padding: 0;
    padding-top: 1*$bfsize;
    margin: 0;
    overflow-x: hidden !important;
}

/* 小于769px */
@media (max-width: 767.8px) {
    #leftBox,
    #rightBox {
        margin-right: 1.6*$bfsize;
        margin-left: 1.6*$bfsize;
    }
}
/*大于992px */
@media (min-width: 768px) {
    #leftBox,
    #rightBox {        
        margin-left: 1.6*$bfsize;
    }
    #leftBox {
        margin-right: 1.6*$bfsize;
    }
    #rightBox {
        margin-right: 1*$bfsize;
    }
}
/*大于992px */
@media (min-width: 992px) {
    .essay-main {
        display: flex;
        justify-content: space-between;
       
        > #leftBox {
            display: flex;
            flex-direction: column;
            width: $leftWidth;
            vertical-align: top;

        }

        > #rightBox {
            padding-right: #{$leftWidth+10};
            margin-right: -#{$leftWidth+10};
            margin-left:0;
            display: flex;
            flex-direction: column;
            padding-left: 1*$bfsize;
            box-sizing: border-box;
            width: 100%;
            vertical-align: top;
        }
    }
}

/* Begin 我的标签相关 */

.aside > .content
{
    li {
        @include defaulta;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-bottom: 4px !important;
    
        .rank {
            padding: 0 6px;
            font-size: 14px;
            margin-right: 4px;
        }
    
        a {
            color: #4f4f4f;
        }
    
        span {
            margin-left: 4px;
        }
    }    
}

#hot{
    li{
        span{
            display: inline-block;
            min-width: 3em;
            text-align: center;
        }
    }
}
#essayTypes {
    ul {
        display: block;
        /*columns: 2;*/
        li {
            /*display: inline-block;*/
            display: flex;
            /*justify-content: space-between;*/
            align-items: center;
            > .badge {
                width: 2em;
                margin-left: 10px;
            }
        }
    }
}
#tags{
    ul{
        display: block;
        li{
            display: inline-block;
            border: 1px solid #efefef;
            // border-radius: 0.2rem;
            >.badge {
                width: 2em;
            }
        }
    }    
}


